.site-list {
  margin: 0 auto;
  width: 176px;
  padding-bottom: 161px;

  text-align: center;
}

.site-list__item--active {
  margin-bottom: 55px;
}

.site-list__item {
  margin-bottom: 50px;
}

.site-list__item a {
  color: rgba(255, 255, 255, 0.5);
  font-size: 15px;
  line-height: 17px;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 0.09em;
  font-style: normal;
  font-weight: normal;
}

.site-list__item--active a {
  color: white;
  font-size: 25px;
  line-height: normal;
  text-transform: uppercase;
  letter-spacing: 0.09em;
  text-align: center;
  font-style: normal;
  font-weight: normal;
}



@media (min-width: $tablet-width) {
  .site-list {
    width: 307px;
  }

  .site-list__item a {
    font-size: 30px;
    line-height: 33px;
  }

  .site-list__item--active a {
    font-size: 45px;
    line-height: 50px;
  }

  .site-list__item {
    margin-bottom: 57px;
  }

  .site-list__item--active {
    margin-bottom: 70px;
  }
}



@media (min-width: $desktop-width) {

}
