.footer-list__item a {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;

  color: rgba(255, 255, 255, 0.5);
}

.footer-list {
  position: relative;
  margin-top: 30px;
  margin-bottom: 35px;
}

.footer-list::before {
  position: absolute;
  content: " ";

  top: -30px;
  left: 15px;

  width: 288px;
  height: 1px;

   background-color: rgba(255, 255, 255, 0.2);
}

.footer-list__item {
  text-align: center;
  margin-bottom: 15px;
}

@media (min-width: $tablet-width) {
  .footer-list::before {
    top: -40px;
    width: 726px;
  }

  .footer-list {
    margin-top: 40px;
    margin-bottom: 40px;
  }

  .footer-list__item a {
    font-size: 20px;
    line-height: 22px;
  }

  .footer-list__item {
    margin-bottom: 20px;
  }
}

@media (min-width: $desktop-width) {
  .footer-list::before {
    top: -21px;
    left: -463px;
    width: 1920px;
  }

  .footer-list {
    margin-left: 463px;
    margin-bottom: 20px;
    width: 1000px;
    display: flex;
    justify-content: space-between;
  }

  .footer-list__item {
    margin-bottom: 0;
  }
}
