.page-header__wrapper {
  display: flex;
  flex-direction: column;

  margin-top: 15px;
}

.page-header__logo {
  margin-left: 30px;
  margin-bottom: 20px;
  z-index: 2;
}

.page-header__logo--open {
  margin: 0 auto;
  margin-top: 25px;
  margin-bottom: 77px;
}

.page-header__info {
  background-image: url("../../img/mobile1-1.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 466px;
}

@media (min-width: $tablet-width) {
  .page-header__logo img {
    width: 137px;
    height: 47px;
  }

  .page-header__info {
    background-image: url("../../img/tablet1.png");
    height: 1024px;
    margin-top: -87px;
  }
}



@media (min-width: $desktop-width) {
  .page-header__info {
    position: relative;
    background-image: $background-color url("../../img/desktop1.jpg");
    height: 1044px;
    background-size: cover;
    margin-top: -190px;
  }

  .page-header__info::before {
    position: absolute;
    content: "";

    top: 970px;

    width: 100%;
    height: 75px;

    background: linear-gradient(0deg, #3B181E 3.33%, rgba(59, 24, 30, 0) 100%);
  }

  .page-header__wrapper {
    margin-top: 87px;
  }

  .page-header__logo {
    margin-left: 96px;
    z-index: 11;
  }
}
