html,
body {
  min-width: 320px;
}

body {
  padding: 0;
  margin: 0 auto;
  min-width: 320px;
  font-family: 'Forum', cursive;
  font-size: 12px;
  line-height: 16px;
  color: white;
  background-color: $background-color;
}

@media (min-width: $desktop-width) {
  body {
    width: auto;
  }
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

a {
  text-decoration: none;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
