.nav {
    position: relative;
}

.nav__button {
  display: none;
}

.nav--opened .nav {
  min-height: 568px;
}

.nav__toggle {
    position: absolute;
    top: -50px;
    right: 30px;
    z-index: 2;

    display: block;
    width: 50px;
    height: 30px;

    background-color: $background-color;

    cursor: pointer;
    border: none;
}

.nav--closed .nav__toggle::before {
    content: "";

    position: absolute;
    top: 10px;
    left: 8px;

    width: 36px;
    height: 1px;
    background-color: #ffffff;
    box-shadow: 0 8px 0 0 #ffffff;
}

.nav--closed .nav__toggle:active::before {
    box-shadow: 0 8px 0 0 rgba(255, 255, 255, 0.3);
}

.nav--opened .nav__toggle {
    &::before,
    &::after {
        content: "";

        position: absolute;
        top: 15px;
        right: 10px;

        width: 31px;
        height: 1px;

        background-color: #ffffff;
    }

    &::before {
        transform: rotate(45deg);
        box-shadow: none;
    }

    &::after {
        transform: rotate(-45deg);
    }

    &:active::before,
    &:active::after {
        background-color: rgba(255, 255, 255, 0.3);
    }
}

.nav--closed .nav__list {
    display: none;
}

@media (min-width: $tablet-width) {

}



@media (min-width: $desktop-width) {
  .nav__button {
    display: block;
    text-align: end;
    margin-top: -44px;
    margin-right: 90px;

    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 22px;
    letter-spacing: 0.06em;
  }

  .nav__toggle {
    right: 155px;
    z-index: 11;
  }

  .nav {
    position: relative;
  }

  .nav--opened .nav__wrapper {
    position: absolute;
    top: -160px;
    right: 0;
    left: 0;
    z-index: 10;
    min-height: 80vh;
    background-color: $background-color;
  }
  .nav__list {
    padding-top: 300px;
  }
}
