.slider__item-page {
  position: relative;
  height: 392px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  padding-left: 20px;
  padding-bottom: 15px;
  padding-top: 20px;

  &--first {
  background-image: url("../../img/mobile2.jpg");
  }
  &--second {
    background-image: url("../../img/mobile1-2.jpg");
  }
  &--third {
    background-image: url("../../img/mobile1-3.jpg");
  }
  &--fourth {
    background-image: url("../../img/mobile1-4.png");
  }
}

.slider__item-page span {
  margin-left: 80px;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 22px;
  text-transform: uppercase;
  color: #FFFFFF;
}

.slider__item-page a {
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 27px;

  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #FFFFFF;
  opacity: 0.9;
}

.slider__item-page span::before {
  position: absolute;
  content: " ";

  top: 30px;
  left: 20px;

  width: 70px;
  height: 1px;

  background-color: #FFFFFF;
}

