@media (min-width: $tablet-width) {
  .slider__item {
    flex: 0 0 50%;
    max-width: 50%;
  }

  .slider-page {
    height: 600px;
    width: 688px;
  }

  .slider {
    max-width: 768px;
    margin-left: auto;
    margin-right: auto;
  }

  //.slider__indicators {
  //  bottom: 24px;
  //  left: 115px;
  //}

  //.slider__indicators li:nth-child(2),
  //.slider__indicators li:nth-child(3) {
  //  display: none;
  //}

  .slider__item-page {
    width: 330px;
    height: 450px;
    margin-right: 28px;

    &--first {
      background-image: url("../../img/tablet2-1.jpg");
    }
  }

  .slider-page__header {
    position: relative;
    font-size: 30px;
    line-height: 33px;

    margin-left: 271px;
    margin-bottom: 52px;
  }

  .slider-page__header::before {
    position: absolute;
    content: " ";

    top: 55px;
    left: 60px;

    width: 85px;
    height: 2px;
    background-color: #FFFFFF;
  }
  .slider__control_right {
    right: 14px;
  }
}

@media (min-width: $desktop-width) {
  .slider {
    max-width: 1920px;
  }

  .slider-page {
    margin-top: 170px;
    height: 600px;
    width: 1650px;
  }

  .slider__item {
    flex: 0 0 25%;
    max-width: 25%;
  }

  .slider__control_right {
    right: 80px;
  }

    .slider-page__header {
      margin-bottom: 120px;
      text-align: center;
      margin-left: 0;
    }

    .slider-page__header::before {
      left: 780px;
    }

    .slider__item-page {
      margin-right: 60px;
    }
}
