.special {
  width: 320px;
  margin: 0 auto;
}

.special__wrapper {
  width: 288px;
  margin-left: 16px;
  margin-top: 62px;
}

.special__img--mobile {
  margin-bottom: 25px;
}

.special__img--tablet {
  display: none;
}

.special__img--decktop {
  display: none;
}

.special__header {
  position: relative;
  width: 145px;
  margin-bottom: 40px;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 34px;

  letter-spacing: 0.06em;
  text-transform: uppercase;
}

.special__header::before {
  position: absolute;
  content: " ";

  top: 85px;
  left: 0;

  width: 85px;
  height: 1px;

  background-color: #FFFFFF;
}

.special__description {
  margin-bottom: 40px;

  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
}

.special__button {
  font-style: normal;
  font-weight: normal;
  font-size: 11px;
  line-height: 12px;
  letter-spacing: 0.15em;
  text-transform: uppercase;
  color: #FFFFFF;

  margin-bottom: 100px;
  padding: 18px 30px 17px 31px;
  background-color: #010001;
  border: none;
}

@media (min-width: $tablet-width) {
  .special {
    width: $tablet-width;
  }

  .special__wrapper {
    margin-left: 172px;
    width: 450px;
  }

  .special__img--mobile {
    display: none;
  }

  .special__img--tablet {
    display: block;
    margin-bottom: 30px;
  }

  .special__header {
    width: 450px;
    margin-bottom: 55px;
    text-align: center;

    font-size: 30px;
    line-height: 51px;
  }

  .special__description {
    margin-bottom: 15px;
    text-align: center;
  }

  .special__button {
    margin-left: 115px;
  }

  .special__header::before {
    left: 180px;
  }
}

@media (min-width: $desktop-width) {
  .special__wrapper {
    margin-top: 170px;
    margin-bottom: 170px;
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
  }

  .special__img--tablet {
    display: none;
  }

  .special__img--decktop {
    display: block;
  }

  .special__description-wrapper {
    margin-right: 164px;
    margin-top: 46px;
  }

  .special__header {
    width: 352px;
    margin-bottom: 40px;
    text-align: left;
  }

  .special__description {
    margin-bottom: 45px;
    text-align: left;
  }

  .special__button {
    margin-left: 0;
  }

  .special__header::before {
    top: 120px;
    left: 0;
  }
}
