.main-info__wrapper {
  width: 234px;
  margin: 0 auto;
  padding-top: 107px;
}

.main-info__header {
  margin-bottom: 38px;
  font-size: 30px;

  font-family: 'Playfair Display', serif;
  font-style: normal;
  font-weight: normal;
  line-height: 42px;

  text-align: center;
  letter-spacing: 0.09em;
  text-transform: uppercase;
}

.main-info__button {
  font-style: normal;
  font-weight: normal;
  font-size: 10px;
  line-height: 11px;
  text-align: center;
  letter-spacing: 0.15em;
  text-transform: uppercase;
  padding: 19px 30px 17px 30px;
  margin-left: 27px;

  background: rgba(0, 0, 0, 0.4);
  border: 1px solid #FFFFFF;
  color: #FFFFFF;
}

.main-info__wrapper-icon {
  width: 260px;
  margin: 0 auto;

  &--footer {
    width: 250px;
    margin-bottom: 30px;
  }
}

.main-info__list-icon {
  display: flex;
  justify-content: space-between;
  margin-top: 74px;
}

.main-info__item {
  background-color: $background-color;
  padding: 14px 9px 5px 9px;
  border-radius: 20px;
}

@media (min-width: $tablet-width) {
  .main-info__wrapper {
    padding-top: 331px;
    width: $tablet-width;
    margin: 0 auto;
  }

  .main-info__header {
    font-family: 'Forum', cursive;
    font-size: 70px;
    line-height: 77px;
    margin-bottom: 82px;
  }

  .main-info__button {
    font-size: 15px;
    line-height: 17px;
    padding: 22px 40px 21px 40px;
    margin-left: 256px;
  }

  .main-info__wrapper-icon {
    width: 460px;

    &--footer {
      width: 250px;
      margin-bottom: 40px;
    }
  }

  .main-info__list-icon {
    margin-top: 211px;
  }

  .main-info__item {
    padding: 28px 12px 8px;
  }

  .main-info__item svg {
    width: 28px;
    height: 28px;
  }

  .main-info__list-icon--footer {
    margin-top: 0;
  }
}

@media (min-width: $desktop-width) {
  .main-info__header {
    font-size: 120px;
    line-height: 132px;
  }

  .main-info__button {
    font-size: 12px;
    line-height: 13px;
    padding: 19px 30px 16px 30px;
    margin-left: 256px;
  }

  .main-info__list-icon {
    margin-top: 41px;
    flex-direction: column;

    &--footer {
      flex-direction: inherit;
      width: 260px;
      margin-top: 0;
      margin-left: 760px;
    }
  }

  .main-info__wrapper-icon {
    width: 1920px;
    margin-left: 90px;

    &--footer {
      margin-bottom: 0;
    }
  }

  .main-info__item {
    background-color: transparent;
  }

  .main-info__item svg {
    width: 25px;
    height: 25px;
    margin-bottom: 24px;
  }

  .main-info__list-icon li {
    z-index: 11;
  }
}
